.button {
    padding: 5px 24px;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: #fff;
    border-radius: 3px;
    text-transform: capitalize;
    cursor: pointer;
    transition: all 0.3s ease-in;
}

.disabled {
    background: #004d99;
    opacity: 0.5;
    color: #002e5c;
    cursor: default;
}

.secondary {
    border: 1px solid #06c;
    background: transparent;
    color: #06c;
}

.default {
    background: #06c;
    border-color: #06c;
}

.red {
    background: #cc1439;
    border-color: #cc1439;
}

.default:hover {
    background: #0080ff;
}

.red:hover {
    background: #ff1a47;
}

.secondary:hover {
    background:  #e5f0fa;
}


.default:active {
    background: #0059b2;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b21232;
}

.button:disabled {
    background:  #004d99;
}
