.greetingForm {
}

.inputAndButtonContainer {
    display: flex;
    gap: 12px;
}

.error {
    margin-top: 7px;
    color: #c00;
    font-size: 14px;
}

.input {
    padding: 4px 12px;
    width: 372px;
    height: 100%;
    max-height: 100%;
    font-weight: 500;
    font-size: 16px;
    color: #000;
    border: 2px solid #D1D1D1;
    border-radius: 4.5px;
}

.input:focus {
}

.errorInput {
    border-color: #CC0000;
}

.button {
    border: none;
    padding: 8px 24px;
    color: #fff;
    font-size: 14px;
    line-height: 143%;
    font-weight: 600;
    background-color: #0066CC;
    text-transform: capitalize;
    border-radius: 3px;
    cursor: pointer;
}

.button:focus {
}

.button:active {
}

.button:disabled {
    cursor: default;
    opacity: 0.5;
}

.text {
    font-size: 14px;
    line-height: 17px;
    color: #00000080;
    margin-bottom: 9px;
}

